/*公共样式*/
* {
  margin: 0;
  padding: 0;
}
html,
body {
  height: 100%;
}
html {
  background: #eeeced;
}
.clearfix:after {
  content: "";
  visibility: hidden;
  display: block;
  clear: both;
  height: 0;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
a,
input,
select,
button,
span,
label,
div,
textarea {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -moz-tap-highlight-color: rgba(0, 0, 0, 0);
  -mz-tap-highlight-color: rgba(0, 0, 0, 0);
  -o-tap-highlight-color: rgba(0, 0, 0, 0);
}
input,
select,
button,
span,
label {
  outline: none;
  border: none;
}
textarea {
  resize: none;
}
.theme {
  margin-top: 20px;
  font-size: 28px;
}
[data-dpr="2"] .theme {
  font-size: 56px;
}
[data-dpr="3"] .theme {
  font-size: 84px;
}
.content {
  margin-top: 30px;
  font-size: 50px;
  color: rebeccapurple;
}
.butBox {
  margin-top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.butBox .but {
  line-height: 2rem;
  width: 7rem;
}
.but {
  font-size: 26px;
  font-family: 'Comic Sans MS', sans-serif;
  background: #a1a9bc;
  border: #c9c0d3 1px solid;
}
.wrap {
  width: 8rem;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -60%);
          transform: translate(-50%, -60%);
  background: #c5b9a1;
  padding: 0.5rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#app {
  font-family: 'Lato', "SCREEN GP Sans SC", "Microsoft YaHei", sans-serif;
  text-align: center;
  height: calc(100% - 1rem);
  width: 10rem;
  margin: 0 auto;
  padding-top: 1rem;
  color: #2c3e50;
}
