.selectBox {
  font-weight: normal;
  font-size: 14px;
  display: inline-block;
  position: relative;
  width: 140px;
  line-height: 32px;
  height: 34px;
  border-radius: 2px;
}
.selectBox .sel_selected {
  text-align: left;
  height: 34px;
  padding-left: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #797681;
  background: #fae8d4 url(data:image/png;base64,) no-repeat 122px;
}
.selectBox .sel_box {
  position: absolute;
  left: 0;
  top: 33px;
  width: 100%;
  z-index: 1;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #fae8d4;
  border: 1px solid #797681;
}
.selectBox .sel_box .sel_input {
  width: 100%;
  line-height: 35px;
  text-align: center;
  border-bottom: 1px solid #797681;
  position: relative;
}
.selectBox .sel_box .sel_input input {
  width: 130px;
  height: 25px;
  border-radius: 35px;
  padding-left: 10px;
  border: 1px solid #797681;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #fae8d4;
}
.selectBox .sel_box .sel_input .search {
  display: inline-block;
  position: absolute;
  width: 20px;
  height: 20px;
  right: 10px;
  top: 8px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 100%;
  cursor: pointer;
}
.selectBox .sel_box ul.sel_list {
  width: 100%;
  height: 95px;
  overflow: auto;
  background-color: #fae8d4;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.selectBox .sel_box ul.sel_list li {
  overflow: hidden;
  height: 31px;
  line-height: 31px;
  text-align: center;
}
.selectBox .sel_box ul.sel_list li:hover {
  background: #c9c0d3;
  cursor: pointer;
}

.fastComputation {
  height: 100%;
  background: url(../../static/img/train_bk.70ea1c3.png) no-repeat center 75%;
  background-size: 70% 50%;
}
.fastComputation .answer {
  margin-top: 20px;
}
.fastComputation .answer input {
  vertical-align: top;
  line-height: 35px;
  width: 5rem;
  padding-left: 10px;
  font-size: 20px;
  border: #c9c0d3 1px solid;
  -webkit-box-shadow: #c9c0d3 1px 1px 5px 2px;
          box-shadow: #c9c0d3 1px 1px 5px 2px;
  margin-right: 10px;
}
